.landing-page {
  font-family: $regular-helvetica;
  a {
    text-decoration: none;
  }
  .landing-hero {
    background-color: #0f1e41;

    .nav-wrapper {
      padding-top: 1em;
      background-color: #0f1e41;
      height: 100px;
      a {
        padding-left: 0px !important;
        @media screen and (max-width: 600px) {
          max-width: 140px;
        }
      }

      .wrap-btn {
        float: right;
        height: 50px;
        padding: 15px 15px;
        font-size: 18px;
        line-height: 20px;
        button {
          background-color: #10a3ff;
          color: white;
          border-radius: 25px;
          padding: 15px 25px;
          outline: none;
          border: transparent;
          @media screen and (max-width: 600px) {
            padding: 10px 15px;
          }
          &:focus {
            outline: none;
          }
          a {
            text-decoration: none;
            color: white;
          }
        }
      }
    }
    .hero-section {
      padding: 4em 0;
      overflow-x: hidden;
      @media screen and (max-width: 600px) {
        padding-bottom: 10em;
      }
      .hero-caption {
        h1 {
          text-align: left;
          margin-top: 0px;
          font-family: $helvetica-bold;
          color: #10a3ff;
          max-width: 500px;
          font-size: 55px;
        }
        .caption-subtitle {
          max-width: 350px;
          span {
            color: white;
            font-family: $regular-helvetica;
            font-size: 16px;
          }
        }
      }
      .coming-soon {
        margin-top: 4em;
        color: #10a3ff;
        img {
          padding-right: 1em;
        }
      }
      .hero-image {
        img {
          right: 45%;
          position: relative;
          width: 140%;
          bottom: 20px;
          @media screen and (max-width: 600px) {
            position: relative;
            width: 100%;
            top: 30px;
            right: 0;
          }
        }
      }
    }
  }
  .hero-image-mobile {
    img {
      position: relative;
      bottom: 140px;
      width: 100%;
    }
  }

  .trusted-section {
    .trusted-wrapper {
      margin-top: 3em;
      margin-bottom: 3em;
      padding: 2em 0;

      .heading_caption {
        margin: 0 auto;
        text-align: center;
        width: 75%;
        h2 {
          color: #0f1e41;
          font-family: $helvetica-bold;
        }
        span {
          padding-top: 2em;
          display: block;
          font-family: $sans;
          font-size: 17px;
          color: #0f1e41;
        }
      }
      .school-animated {
        position: relative;
        img.school {
          width: 90%;
        }
        .one {
          width: 5%;
          position: absolute;
          left: 10%;
          top: 10%;
        }
        .two {
          width: 5%;
          position: absolute;
          left: 0%;
          top: 20%;
        }
        .three {
          width: 5%;
          position: absolute;
          bottom: 55%;
        }
        .four {
          width: 5%;
          bottom: 35%;
          position: absolute;
          left: 0%;
        }
        .five {
          width: 5%;
          position: absolute;
          right: 20%;
        }
        .five {
          width: 5%;
          position: absolute;
          /* right: 16%; */
          top: 10%;
        }
        .six {
          width: 5%;
          position: absolute;
          right: 12%;
          top: 28%;
        }
        .seven {
          width: 5%;
          position: absolute;
          right: 3%;
          bottom: 55%;
        }
        .eight {
          width: 5%;
          position: absolute;
          right: 0%;
          bottom: 30%;
        }

        &:hover {
          @keyframes straightLine {
            50% {
              transform: translate3D(100px, 100px, 0);
              animation-timing-function: cubic-bezier(0.3, 0.27, 0.07, 1.64);
            }
          }
          @keyframes straightLine {
            50% {
              transform: translate3D(100px, 100px, 0);
              animation-timing-function: cubic-bezier(0.02, 0.01, 0.21, 1);
            }
          }
          @keyframes secondLine {
            50% {
              transform: translate3D(-200px, 100px, 0);
            }
          }

          .one {
            width: 5%;
            //animation: straightLine 4.5s infinite linear;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-direction: alternate;
          }
          .two {
            width: 5%;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-delay: 2s;
            animation-direction: alternate-reverse;
          }
          .three {
            width: 5%;
            animation: straightLine 4.5s infinite linear;
          }
          .four {
            width: 5%;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-direction: alternate;
            animation-delay: 5s;
          }
          .five {
            width: 5%;
            animation: secondLine 4.5s infinite linear;
            animation-direction: reverse;
          }
          .six {
            width: 5%;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-delay: 1s;
            animation-direction: alternate-reverse;
          }
          .seven {
            width: 5%;
            animation: secondLine 5.5s infinite linear;
            animation-direction: reverse;
          }
          .eight {
            width: 5%;
            animation: secondLine 6.5s infinite linear;
          }
        }
      }
    }
    img {
      width: 100%;
    }
  }
}

.featured-section {
