.newsletter {
  background-color: #0082d3;
  max-height: 330px;
  @media screen and (max-width: 768px) {
    max-height: fit-content;
  }
  img {
    position: relative;
    bottom: 20%;

    @media screen and (max-width: 768px) {
      position: relative;
      bottom: 0;
      right: 7%;
    }
  }
  .col-md-7 {
    padding-top: 3em;
    h5 {
      color: white;
      font-family: $sans;
      font-weight: normal;
    }
    h2 {
      font-family: $helvetica-bold;
      color: white;
    }
    form {
      max-width: 500px;
      margin: 0;
      padding: 2em 0;
      .row {
        border-radius: 25px;
        overflow: hidden;

        input {
          border: none;
          outline: none;
          padding: 0.8em 3em;
          width: 100%;
          min-height: 45px;
        }
        button {
          width: 100%;
          padding: 0.9em;
          border: none;
          border-radius: 25px;
          position: relative;
          right: 18%;
          color: white;
          background-color: #00428b;
        }
      }
    }
  }
}

.pre-footer {
  border-top: 1px solid #10a3ff;
  padding-top: 3em;
  span {
    a {
      color: #212353;
      text-decoration: none;
      &:hover {
        text-decoration: none;
      }
    }
  }
  .footer1 {
    padding-bottom: 2em;
    h2 {
      color: #002984;
      font-family: $helvetica-bold;
    }
    h6 {
      color: #10a3ff;
    }
    .button-wrappa {
      margin-top: 20px;
      display: flex;
      justify-content: space-between;
      button.learn-more {
        background-color: #10a3ff;
        color: white;
        border-radius: 20px;
        padding: 10px 30px;
        outline: none;
        border: transparent;
        &:focus {
          outline: none;
        }
      }
      button.request-demo {
        background-color: white;
        color: #10a3ff;
        border-radius: 20px;
        padding: 10px 30px;
        outline: none;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border: transparent;
        &:focus {
          outline: none;
        }
        img {
          padding-left: 1em;
        }
      }
    }
  }
  .footer2 {
    .col-md-5 {
      padding-bottom: 1em;
      img {
        width: 40%;
      }
    }
    .col-xs---6 {
      @media screen and (max-width: 600px) {
        width: 49%;
        padding-left: 15%;
      }
    }
    .level-one {
      display: block;
      padding-bottom: 2em;
      text-align: left;
    }
    .level-two {
      display: block;
      padding-top: 1em;
      text-align: left;
      img {
        width: 25%;
        padding-right: 2%;
      }
    }
  }

  .footer3 {
    .level-one {
      display: block;
      text-align: left;
      padding-bottom: 2em;
    }
    .level-two {
      display: block;
      text-align: left;
    }
  }
  .footer4 {
    .level-one {
      display: block;
      padding-bottom: 2em;
    }
    .level-two {
      display: block;
      img {
        width: 50%;
      }
    }
  }
}
.bottom-footer {
  padding-top: 1em;
  span {
    display: block;
    color: #212353;
  }
}

